const mobileSize = 767;
const tabletMinSize = 768;
const tabletMaxSize = 1024;
const bigDesktopSize = 1281;

export const mobile = `(max-width: ${mobileSize}px)`;
export const tablet = `(min-width: ${tabletMinSize}px) and (max-width: ${tabletMaxSize}px)`;
export const bigDesktop = `(min-width: ${bigDesktopSize}px)`;